import { Currency, GET_ETHER, Token, ChainId } from '@uniswap/sdk'
import { isNativeCoin } from '.'

export function currencyId(currency: Currency, chainId: ChainId): string {
  if (isNativeCoin(chainId, currency)) return GET_ETHER(chainId).symbol?.toUpperCase() as string
  if (currency instanceof Token) return currency.address
  throw new Error('invalid currency')
}
