export const GET_DEXKIT = (chainId: ChainId) => {
  switch (chainId) {
    case ChainId.BSCMAINNET:
      return DEXKIT_BSC;
    case ChainId.MATICMAINNET:
        return DEXKIT_MATIC;
    default:
     return DEXKIT;
  }

}


