import React from 'react'
import { Redirect, RouteComponentProps } from 'react-router-dom'
import { getConfig } from '../../constants/config';

// Redirects to swap but only replace the pathname
export function RedirectPathToSwapOnlyAgg({ location }: RouteComponentProps) {

  let pathname = '/swap';
  const config = getConfig();
  if(config.bsc_as_default){
    pathname = '/bsc/swap';
  } 
  if(config.matic_as_default){
    pathname = '/matic/swap';
  } 
  console.log(location);
  return <Redirect to={{ ...location, pathname: pathname }} />
}

// Redirects from the /swap/:outputCurrency path to the /swap?outputCurrency=:outputCurrency format
export function RedirectToSwapAgg(props: RouteComponentProps<{ outputCurrency: string }>) {
  const {
    location: { search },
    match: {
      params: { outputCurrency }
    }
  } = props
  const config = getConfig();
  let pathname = '/swap';
  if(config.bsc_as_default){
    pathname = '/bsc/swap';
  } 
  if(config.matic_as_default){
    pathname = '/matic/swap';
  } 
  return (
    <Redirect
      to={{
        ...props.location,
        pathname: pathname,
        search:
          search && search.length > 1
            ? `${search}&outputCurrency=${outputCurrency}`
            : `?outputCurrency=${outputCurrency}`
      }}
    />
  )
}
