import { Contract } from '@ethersproject/contracts'
import { getAddress } from '@ethersproject/address'
import { AddressZero } from '@ethersproject/constants'
import { JsonRpcSigner, Web3Provider } from '@ethersproject/providers'
import { BigNumber } from '@ethersproject/bignumber'
import { abi as IUniswapV2Router02ABI } from '@uniswap/v2-periphery/build/IUniswapV2Router02.json'
import { ROUTER_ADDRESS } from '../constants'
import { JSBI, Percent, Token, CurrencyAmount, Currency, GET_ETHER, isBSC, isMATIC, ChainId } from '@uniswap/sdk'
import { TokenAddressMap } from '../state/lists/hooks'

// returns the checksummed address if the address is valid, otherwise returns false
export function isAddress(value: any): string | false {
  try {
    return getAddress(value)
  } catch {
    return false
  }
}

const ETHERSCAN_PREFIXES: { [chainId in ChainId]: string } = {
  1: '',
  3: 'ropsten.',
  4: 'rinkeby.',
  5: 'goerli.',
  42: 'kovan.',
  56: '',
  97: 'testnet.',
  137: '',
}

export function getEtherscanLink(chainId: ChainId, data: string, type: 'transaction' | 'token' | 'address'): string {
  let appendExplorer = 'etherscan.io';
  if(isBSC(chainId)){
    appendExplorer = 'bscscan.com'
  }
  if(isMATIC(chainId)){
    appendExplorer = 'polygonscan.com'
  }


  const prefix = `https://${ETHERSCAN_PREFIXES[chainId] || ETHERSCAN_PREFIXES[1]}${appendExplorer}`

  switch (type) {
    case 'transaction': {
      return `${prefix}/tx/${data}`
    }
    case 'token': {
      return `${prefix}/token/${data}`
    }
    case 'address':
    default: {
      return `${prefix}/address/${data}`
    }
  }
}

export function getEtherscanName(chainId: ChainId): string {

  switch (chainId) {
    case 56: {
      return `Bscan`
    }
    case 96: {
      return `Bscan`
    }
    case 137: {
      return `Polygonscan`
    }
    default: {
      return 'Etherscan'
    }
  }
}

