import configFile from './config.json';


interface Config {
    name: string,
    logo: string,
    logo_dark: string,
    domain: string,
    feeRecipient: string,
    buyTokenPercentage: string,
    affiliateAddress: string,
    brand_color: string,
    brand_color_dark: string,
    support_bsc?: boolean,
    bsc_as_default?: boolean,
    matic_as_default?: boolean,
    default_token_list?: string;
    default_slippage?: number;
    default_token_list_bsc?: string;
    default_token_list_matic?: string;
    default_token_address?: string;
    default_token_address_bsc?: string;
    default_token_address_matic?: string;
    fee_waive_for_default_token?: boolean;
    is_dark_mode?: boolean;
    hide_powered_by_dexkit?: boolean;
    token_fee_program?: {
        token_address: string;
        token_amount: string;
        buyTokenPercentage: string;
    }
    links?:{
        about?: string,
        code?: string,
        docs?: string,
        discord?: string,
        telegram?: string,
        analytics?: string
    },
    wallets?:{
        fortmatic?: string,
        portis?: string; 
    }
}

let config: Config | undefined;

export const getConfig = (): Config => {
    if(!config){       
        if((window as any).parent.dexkit_aggregator){
            const aggParams = (window as any).parent.dexkit_aggregator;
            config = (window as any).parent.dexkit_aggregator?.data?.config?.domain ? (window as any).parent.dexkit_aggregator?.data?.config as Config : (configFile as Array<Config>).find(c => c.domain === 'swap.dexkit.com') as Config; 
            if(aggParams.default_token_address_eth){
               config.default_token_address = aggParams.default_token_eth;
            }
            if(aggParams.default_token_address_bsc){
                config.default_token_address_bsc = aggParams.default_token_bsc;
            }
            if(aggParams.default_token_address_matic){
                config.default_token_address_matic = aggParams.default_token_matic;
            }
            if(aggParams.brand_color){
                config.brand_color = aggParams.brand_color;
            }
            if(aggParams.brand_color_dark){
                config.brand_color_dark = aggParams.brand_color_dark;
            }
            if(aggParams.logo){
                config.logo = aggParams.logo;
            }
            if(aggParams.logo_dark){
                config.logo_dark = aggParams.logo_dark;
            }
            if(aggParams.name){
                config.name = aggParams.name;
            }
            if(aggParams.matic_as_default){
                config.matic_as_default = aggParams.matic_as_default === 'false' ? false : true;
            }
            if(aggParams.bsc_as_default){
                config.bsc_as_default = aggParams.bsc_as_default === 'false' ? false : true;
            }
            if(aggParams.is_dark_mode){
                config.is_dark_mode = aggParams.is_dark_mode === 'false' ? false : true;
            }
            // We need to multiply here by 100, to be according to the UI
            if(config.default_slippage){
                config.default_slippage =  Number(config.default_slippage)*100;
            }
             // If shortcode enabled, does the same and susbcribe the value
            if(aggParams.default_slippage){
                config.default_slippage = Number(aggParams.default_slippage)*100;
            }
           
            return config;
        }

        const domain =  window.location.hostname;
        config = (configFile as Array<Config>).find(c => c.domain === domain);
        if(!config){
           config = (configFile as Array<Config>).find(c => c.domain === 'swap.dexkit.com');
           console.log('Config is not defined for this domain. Contact DexKit support');
           return config as Config;
        }
    }
    return config;
}

export const MIN_KIT_AFFILIATE_AMOUNT = '200';